        >There are {{ compareLength }} plans ready to compare.</span
      >
      <span v-else class="primary--text">Please add 2 or more plans to compare.</span>
      <template v-slot:action="{ attrs }">
        <div class="d-flex justify-space-between" style="width: 100%">
          <v-btn
            v-bind="attrs"
            text
            v-if="compareLength > 1"
            color="indigo"
            class="text-none"
            @click="$store.dispatch('toggleCompare')"
          >
            Compare plans
          </v-btn>

          <v-btn color="red" class="text-none" text v-bind="attrs" @click="show = false">
            Dismiss
          </v-btn>
        </div>
      </template>
    </v-snackbar>
