    >  <v-card tile flat elevation="0">
        <v-card-title style="font-family: 'Inter', sans-serif !important" class="text-h6">{{
          compareTitle
        }}</v-card-title> -->
    <!--<v-container class="text-center d-flex justify-center align-center">
          <v-card outlined class="mx-2" v-for="(item, i) in compare" :key="i">
            <v-card-title
              :class="[
                item.plan.rating ? item.plan.rating.toLowerCase() : 'primary',
                'white--text',
                'text-subtitle-1sd',
              ]"
