    </v-bottom-sheet>-->
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data: () => ({
    show: true,
  }),
  methods: {},
  computed: {
    compareTitle() {
      let showTitle = 'Comparing' + this.$store.getters.compareLength + 'plans'
      let smallTitle = this.$store.getters.compareLength + ' plans ready for comparison'
      return this.$store.getters.showCompare ? showTitle : smallTitle
    },
    /* show() {
      return this.$store.getters.compareLength > 0 && !this.$store.getters.showCompare
    }, */
    ...mapGetters(['compare', 'compareLength', 'showCompare']),
  },
}
